.notifications-list ul li{
    position: relative;
    padding-bottom: 25px;
    padding-left: 87px;
    margin-bottom: 25px;
    border-bottom: 1px solid #ECECEC;
}
.notifications-list ul li:last-child{
    margin-bottom: 0;
    border-bottom-color: transparent;
}
.notifier-img{
    width: 63px;
    height: 47px;  
    border-radius: 4px;
    overflow: hidden; 
    position: absolute;
    left: 0;
    top: 0;
}
.notifier-img img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
}

.notification-text{
    padding-right: 70px;
}
.notification-controls{
    position: absolute;
    right: 0;
    top: 0;
}
.notification-controls .btn{
    padding: 0 5px;
    line-height: 0;
}
.notification-controls .dropdown-toggle::after{
    content: url('../../images/icons/ic-menus.svg');
    border:0;
}

.notification-controls .dropdown-menu{
    border-color: transparent;
    box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.16), 0px 0px 0px 1px rgba(0, 0, 0, 0.06);
    border-radius: 5px 0px 5px 5px;
}
.notification-controls .dropdown-item{
    display: flex;
    align-items: center;
    font-size: 18px;
    color: #545454;
    border-bottom: 1px solid #ECECEC;
    padding: 15px 15px;
}
.notification-controls .dropdown-item:last-child{
    border-bottom-color: transparent;
}

.notification-controls .dropdown-item img{
    margin-right: 15px;
}

.notification-time{
    display: block;
    font-size: 14px;
    color: #B0B0B0;
    position: absolute;
    right: 0;
    bottom: 30px;
}

@media screen and (max-width: 767px){
    .notification-text{
        padding-right: 30px;
    }
    .notifications-list ul li{
        padding-left: 67px;
    }
    .notifier-img{
        width: 52px;
        height: 39px;
    }
}